{/* --- THE SUPPORT ISLAND (MATCHED TO UPLOADED IMAGE) --- */}
<section className="w-full flex justify-center pb-24">
  <div className="w-[90%] max-w-7xl bg-[#E8E8FF] rounded-none p-12 md:p-20 flex flex-col lg:flex-row gap-0 items-stretch relative overflow-hidden">
    
    {/* Decorative Dot Pattern (Right Side) */}
    <div className="absolute right-1/4 top-1/2 -translate-y-1/2 opacity-30 pointer-events-none">
      <div className="grid grid-cols-5 gap-3">
        {[...Array(25)].map((_, i) => (
          <div key={i} className="w-1 h-1 bg-white rounded-full" />
        ))}
      </div>
    </div>

    {/* LEFT: FORM CONTAINER */}
    <div className="w-full lg:w-[65%] bg-white p-10 shadow-sm relative z-10">
      <h3 className="text-xl font-bold text-slate-900 mb-10">Submit a Support Request</h3>
      
      <form className="space-y-4">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <input placeholder="First Name" className="w-full p-4 bg-white border border-slate-200 rounded-sm text-sm outline-none placeholder:text-slate-300" />
          <input placeholder="Last Name" className="w-full p-4 bg-white border border-slate-200 rounded-sm text-sm outline-none placeholder:text-slate-300" />
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <input placeholder="Company" className="w-full p-4 bg-white border border-slate-200 rounded-sm text-sm outline-none placeholder:text-slate-300" />
          <input placeholder="Email" className="w-full p-4 bg-white border border-slate-200 rounded-sm text-sm outline-none placeholder:text-slate-300" />
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <input placeholder="Phone" className="w-full p-4 bg-white border border-slate-200 rounded-sm text-sm outline-none placeholder:text-slate-300" />
          <div className="relative">
            <select className="w-full p-4 bg-white border border-slate-200 rounded-sm text-sm outline-none appearance-none text-slate-400">
              <option>Priority</option>
              <option>Normal</option>
              <option>High</option>
              <option>Urgent</option>
            </select>
            <ChevronDown size={14} className="absolute right-4 top-1/2 -translate-y-1/2 text-slate-400" />
          </div>
        </div>
        <textarea placeholder="Description" rows={5} className="w-full p-4 bg-white border border-slate-200 rounded-sm text-sm outline-none resize-none placeholder:text-slate-300" />
        
        <div className="flex items-center gap-3 py-4">
          <input type="checkbox" className="w-4 h-4 border-slate-300 rounded-sm" />
          <span className="text-[11px] text-slate-500 font-medium">
            I am informed about processing of my personal data and the right to withdraw my consent*
          </span>
        </div>
        
        <button className="bg-[#4100D1] text-white px-10 py-4 rounded-sm font-bold text-xs uppercase tracking-widest hover:bg-slate-900 transition-all">
          Submit
        </button>
      </form>
    </div>

    {/* RIGHT: TRUST METRICS (THE VERTICAL STACK) */}
    <div className="w-full lg:w-[35%] flex flex-col justify-center px-12 lg:px-20 space-y-12 relative z-10">
      
      {/* Clutch Review Section */}
      <div className="flex flex-col gap-1">
        <p className="text-[10px] font-bold text-[#FF3B3B] uppercase tracking-[0.2em]">Reviewed on</p>
        <div className="flex items-baseline gap-3">
          <span className="text-2xl font-black text-[#0f172a] tracking-tighter">Clutch</span>
          <div className="flex text-[#FF3B3B] gap-0.5">
            {[...Array(5)].map((_, i) => <Star key={i} size={14} fill="currentColor" />)}
          </div>
        </div>
        <p className="text-[10px] font-bold text-slate-400 uppercase tracking-widest">31 Reviews</p>
      </div>

      {/* Metrics List */}
      <div className="space-y-10">
        <div className="border-b border-slate-200 pb-6">
          <div className="flex items-baseline gap-2">
            <span className="text-4xl font-black text-slate-900 tracking-tight">20</span>
            <span className="text-lg font-bold text-slate-900">Years</span>
          </div>
          <p className="text-xs font-medium text-slate-500 uppercase tracking-widest mt-1">Proven Track Record</p>
        </div>

        <div className="border-b border-slate-200 pb-6">
          <div className="flex items-baseline gap-2">
            <span className="text-4xl font-black text-slate-900 tracking-tight">98</span>
            <span className="text-lg font-bold text-slate-900">%</span>
          </div>
          <p className="text-xs font-medium text-slate-500 uppercase tracking-widest mt-1">Customer Satisfaction</p>
        </div>

        <div>
          <div className="flex items-baseline gap-2">
            <span className="text-4xl font-black text-slate-900 tracking-tight">3</span>
            <span className="text-lg font-bold text-slate-900">Mins</span>
          </div>
          <p className="text-xs font-medium text-slate-500 uppercase tracking-widest mt-1">Average Answer Time</p>
        </div>
      </div>
    </div>
  </div>
</section>